import type { FC } from '../../../../lib/teact/teact';
import React, {
  memo, useCallback, useEffect, useMemo, useRef, useState,
} from '../../../../lib/teact/teact';
import {
  getActions, getGlobal, setGlobal, withGlobal,
} from '../../../../global';

import type { ApiChatlistExportedInvite, ApiSticker, ApiThemeParameters } from '../../../../api/types';
import type { CustomEmojiIconsFolder } from '../../../../global/types';
import type {
  FolderEditDispatch,
  FoldersState,
} from '../../../../hooks/reducers/useFoldersReducer';
import type { IAnchorPosition } from '../../../../types';

import { EMOJI_SIZE_PICKER, STICKER_SIZE_FOLDER_SETTINGS } from '../../../../config';
import { requestMeasure } from '../../../../lib/fasterdom/fasterdom';
import { isUserId } from '../../../../global/helpers';
import { selectCanPlayAnimatedEmojis, selectCanShareFolder } from '../../../../global/selectors';
import { selectCurrentLimit } from '../../../../global/selectors/limits';
import { IS_TOUCH_ENV } from '../../../../util/browser/windowEnvironment';
import buildClassName from '../../../../util/buildClassName';
import buildStyle from '../../../../util/buildStyle';
import { findIntersectionWithSet } from '../../../../util/iteratees';
import { MEMO_EMPTY_ARRAY } from '../../../../util/memo';
import { CUSTOM_PEER_EXCLUDED_CHAT_TYPES, CUSTOM_PEER_INCLUDED_CHAT_TYPES } from '../../../../util/objects/customPeer';
import { extractCurrentThemeParams } from '../../../../util/themeStyle';
import { LOCAL_TGS_URLS } from '../../../common/helpers/animatedAssets';

import { selectChatFilters } from '../../../../hooks/reducers/useFoldersReducer';
import useColorFilter from '../../../../hooks/stickers/useColorFilter';
import useAppLayout from '../../../../hooks/useAppLayout';
import useFlag from '../../../../hooks/useFlag';
import useHistoryBack from '../../../../hooks/useHistoryBack';
import useLastCallback from '../../../../hooks/useLastCallback';
import useMouseInside from '../../../../hooks/useMouseInside';
import useOldLang from '../../../../hooks/useOldLang';

import AnimatedIconWithPreview from '../../../common/AnimatedIconWithPreview';
import FolderIconPicker from '../../../common/FolderIconPicker';
import GroupChatInfo from '../../../common/GroupChatInfo';
import Icon from '../../../common/icons/Icon';
import PrivateChatInfo from '../../../common/PrivateChatInfo';
import StickerView from '../../../common/StickerView';
import Button from '../../../ui/Button';
import FloatingActionButton from '../../../ui/FloatingActionButton';
import InputText from '../../../ui/InputText';
import ListItem from '../../../ui/ListItem';
import Menu from '../../../ui/Menu';
import Portal from '../../../ui/Portal';
import ResponsiveHoverButton from '../../../ui/ResponsiveHoverButton';
import Spinner from '../../../ui/Spinner';
import {
  DEFAULT_FOLDER_ICON,
  getIconNameByFolder,
  getLocalStorageFolderIcons,
  LOCSTOR_CUSTOM_EMOJI_KEY,
} from '../../main/ChatFolders';

type OwnProps = {
  state: FoldersState;
  dispatch: FolderEditDispatch;
  onAddIncludedChats: VoidFunction;
  onAddExcludedChats: VoidFunction;
  onShareFolder: VoidFunction;
  onOpenInvite: (url: string) => void;
  isActive?: boolean;
  isOnlyInvites?: boolean;
  onReset: () => void;
  onBack: () => void;
  onSaveFolder: (cb?: VoidFunction) => void;
};

type StateProps = {
  customSticker?: ApiSticker;
  customEmoji?: string;
  loadedActiveChatIds?: string[];
  loadedArchivedChatIds?: string[];
  invites?: ApiChatlistExportedInvite[];
  isRemoved?: boolean;
  maxInviteLinks: number;
  maxChatLists: number;
  chatListCount: number;
};

const SUBMIT_TIMEOUT = 500;

const INITIAL_CHATS_LIMIT = 5;

export const ERROR_NO_TITLE = 'Please provide a title for this folder.';
export const ERROR_NO_CHATS = 'ChatList.Filter.Error.Empty';

const SettingsFoldersEdit: FC<OwnProps & StateProps> = ({
  state,
  dispatch,
  onAddIncludedChats,
  onAddExcludedChats,
  onShareFolder,
  onOpenInvite,
  isActive,
  onReset,
  isRemoved,
  onBack,
  customSticker,
  customEmoji,
  loadedActiveChatIds,
  isOnlyInvites,
  loadedArchivedChatIds,
  invites,
  maxInviteLinks,
  maxChatLists,
  chatListCount,
  onSaveFolder,
}) => {
  const {
    loadChatlistInvites,
    openLimitReachedModal,
    showNotification,
  } = getActions();

  const isCreating = state.mode === 'create';
  const isEditingChatList = state.folder.isChatList;
  // eslint-disable-next-line no-null/no-null
  const inputRef = useRef<HTMLInputElement>(null);
  // eslint-disable-next-line no-null/no-null
  const btnRef = useRef<HTMLButtonElement>(null);
  // eslint-disable-next-line no-null/no-null
  const sharedCanvasRef = useRef<HTMLCanvasElement>(null);
  // eslint-disable-next-line no-null/no-null
  const containerRef = useRef<HTMLDivElement>(null);

  const [customStickerSelected, setCustomStickerSelected] = useState<ApiSticker | undefined>(customSticker);
  const [customEmojiSelected, setCustomEmojiSelected] = useState<string | undefined>(customEmoji);

  const [isIncludedChatsListExpanded, setIsIncludedChatsListExpanded] = useState(false);
  const [isExcludedChatsListExpanded, setIsExcludedChatsListExpanded] = useState(false);

  const { isMobile } = useAppLayout();
  const [isOpen, setOpen, setClose] = useFlag();

  useEffect(() => {
    if (isRemoved) {
      onReset();
    }
  }, [isRemoved, onReset]);
  const [, setInputCoordinates] = useState<{
    x: number;
    y: number;
    height: number;
    width: number;
    right: number;
  }>({
    x: 0, y: 0, height: 0, width: 0, right: 0,
  });

  useEffect(() => {
    if (isActive && state.folderId && state.folder.isChatList) {
      loadChatlistInvites({ folderId: state.folderId });
    }
  }, [isActive, state.folder.isChatList, state.folderId]);

  const {
    selectedChatIds: includedChatIds,
    selectedChatTypes: includedChatTypes,
  } = useMemo(() => selectChatFilters(state, 'included'), [state]);
  const {
    selectedChatIds: excludedChatIds,
    selectedChatTypes: excludedChatTypes,
  } = useMemo(() => selectChatFilters(state, 'excluded'), [state]);

  useEffect(() => {
    setIsIncludedChatsListExpanded(false);
    setIsExcludedChatsListExpanded(false);
  }, [state.folderId]);

  const [visibleIncludedChatIds, visibleExcludedChatIds] = useMemo(() => {
    const allLoadedChatsSet = new Set([
      ...(loadedActiveChatIds || []),
      ...(loadedArchivedChatIds || []),
    ]);

    const loadedIncludedChatIds = findIntersectionWithSet(includedChatIds, allLoadedChatsSet);
    const loadedExcludedChatIds = findIntersectionWithSet(excludedChatIds, allLoadedChatsSet);

    return [
      isIncludedChatsListExpanded
        ? loadedIncludedChatIds
        : loadedIncludedChatIds.slice(0, INITIAL_CHATS_LIMIT - includedChatTypes.length),
      isExcludedChatsListExpanded
        ? loadedExcludedChatIds
        : loadedExcludedChatIds.slice(0, INITIAL_CHATS_LIMIT - excludedChatTypes.length),
    ];
  }, [
    excludedChatIds, includedChatIds, includedChatTypes, excludedChatTypes,
    isExcludedChatsListExpanded, isIncludedChatsListExpanded,
    loadedActiveChatIds, loadedArchivedChatIds,
  ]);

  const lang = useOldLang();

  useHistoryBack({
    isActive,
    onBack,
  });

  const handleChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const { currentTarget } = event;
    dispatch({ type: 'setTitle', payload: currentTarget.value.trim() });
  }, [dispatch]);

  const handleSubmit = useCallback(() => {
    dispatch({ type: 'setIsLoading', payload: true });

    const stickerOrEmojiSelected: string | ApiSticker | undefined = customEmojiSelected
    ?? customStickerSelected ?? undefined;

    if (stickerOrEmojiSelected) {
      const newStorage: CustomEmojiIconsFolder = getLocalStorageFolderIcons();
      newStorage[state.folderId ?? -1] = stickerOrEmojiSelected;
      localStorage.setItem(
        LOCSTOR_CUSTOM_EMOJI_KEY,
        JSON.stringify(newStorage),
      );

      if (customStickerSelected) {
        const global = getGlobal();
        global.customEmojis.byId[customStickerSelected.id] = customStickerSelected;
        setGlobal(global);
      }
    }

    onSaveFolder(() => {
      setTimeout(() => {
        onReset();
      }, SUBMIT_TIMEOUT);
    });
  }, [dispatch, customEmojiSelected, customStickerSelected, onSaveFolder, state.folderId, onReset]);

  const handleCreateInviteClick = useCallback(() => {
    if (!invites) {
      if (isCreating) {
        onSaveFolder(onShareFolder);
      }
      return;
    }

    // Ignoring global updates is a known drawback here
    if (!selectCanShareFolder(getGlobal(), state.folderId!)) {
      showNotification({ message: lang('ChatList.Filter.InviteLink.IncludeExcludeError') });
      return;
    }

    if (chatListCount >= maxChatLists && !state.folder.isChatList) {
      openLimitReachedModal({
        limit: 'chatlistJoined',
      });
      return;
    }

    if (invites.length < maxInviteLinks) {
      if (state.isTouched) {
        onSaveFolder(onShareFolder);
      } else {
        onShareFolder();
      }
      return;
    }

    openLimitReachedModal({
      limit: 'chatlistInvites',
    });
  }, [
    invites, state.folderId, state.isTouched, chatListCount, maxInviteLinks, isCreating, onSaveFolder,
    onShareFolder, lang, maxChatLists, state.folder.isChatList,
  ]);

  const handleEditInviteClick = useCallback((e: React.MouseEvent<HTMLElement>, url: string) => {
    if (state.isTouched) {
      onSaveFolder(() => onOpenInvite(url));
    } else {
      onOpenInvite(url);
    }
  }, [onSaveFolder, onOpenInvite, state.isTouched]);

  function renderChatType(key: string, mode: 'included' | 'excluded') {
    const chatType = mode === 'included'
      ? CUSTOM_PEER_INCLUDED_CHAT_TYPES.find(({ type: typeKey }) => typeKey === key)
      : CUSTOM_PEER_EXCLUDED_CHAT_TYPES.find(({ type: typeKey }) => typeKey === key);

    if (!chatType) {
      return undefined;
    }

    return (
      <ListItem
        key={chatType.type}
        className="settings-folders-list-item mb-1"
        narrow
        inactive
      >
        <PrivateChatInfo
          avatarSize="small"
          customPeer={chatType}
        />
      </ListItem>
    );
  }

  function renderChats(mode: 'included' | 'excluded') {
    const selectedChatTypes = mode === 'included' ? includedChatTypes : excludedChatTypes;
    const visibleChatIds = mode === 'included' ? visibleIncludedChatIds : visibleExcludedChatIds;

    const isExpanded = mode === 'included' ? isIncludedChatsListExpanded : isExcludedChatsListExpanded;
    const allChatIds = mode === 'included' ? includedChatIds : excludedChatIds;
    const leftChatsCount = allChatIds.length - visibleChatIds.length;
    const clickHandler = mode === 'included'
      ? () => setIsIncludedChatsListExpanded(true)
      : () => setIsExcludedChatsListExpanded(true);

    return (
      <>
        {selectedChatTypes.map((key) => renderChatType(key, mode))}
        {visibleChatIds.map((id) => (
          <ListItem
            className="settings-folders-list-item mb-1"
            narrow
            inactive
          >
            {isUserId(id) ? (
              <PrivateChatInfo avatarSize="small" userId={id} />
            ) : (
              <GroupChatInfo avatarSize="small" chatId={id} />
            )}
          </ListItem>
        ))}
        {(!isExpanded && leftChatsCount > 0) && (
          <ListItem
            key="load-more"
            className="settings-folders-list-item"
            narrow
            // eslint-disable-next-line react/jsx-no-bind
            onClick={clickHandler}
            icon="down"
          >
            {lang('FilterShowMoreChats', leftChatsCount, 'i')}
          </ListItem>
        )}
      </>
    );
  }

  useEffect(() => {
    const rect = btnRef.current?.getBoundingClientRect();

    if (rect) {
      setInputCoordinates({
        x: rect?.x ?? -10000,
        y: rect?.y ?? -10000,
        height: rect?.height ?? 0,
        width: rect?.width ?? 0,
        right: rect?.right ?? 0,
      });
    }
  }, [btnRef, isOpen, isActive]);
  // isOpen = true;
  const [contextMenuAnchor, setContextMenuAnchor] = useState<IAnchorPosition | undefined>(undefined);
  const [handleMouseEnter, handleMouseLeave] = useMouseInside(isOpen, setClose, undefined, isMobile);

  // eslint-disable-next-line no-null/no-null
  const triggerRef = useRef<HTMLDivElement>(null);
  const handleActivateSymbolMenu = useLastCallback(() => {
    setOpen();
    const triggerEl = triggerRef.current;
    if (!triggerEl) return;
    const { x, y } = triggerEl.getBoundingClientRect();
    setContextMenuAnchor({ x, y });
  });

  const possibleIcon = getIconNameByFolder(state.folder);
  // eslint-disable-next-line no-null/no-null
  const ref = useRef<HTMLDivElement>(null);

  const canAnimate = selectCanPlayAnimatedEmojis(getGlobal());

  const [theme, setTheme] = useState<ApiThemeParameters | undefined>(undefined);
  const accentColorTheme = useColorFilter(theme?.accent_text_color ?? '#000000', true);
  const textColorTheme = useColorFilter(theme?.secondary_text_color ?? '#000000', true);

  useEffect(() => {
    requestMeasure(() => {
      const currTheme = extractCurrentThemeParams();
      setTheme(currTheme);
    });
  }, []);

  const handleClick = useLastCallback(() => {
    if (isOpen) {
      setClose();
    } else {
      setOpen();
    }
  });

  const handleMenuClose = useLastCallback(() => {
    setClose();
  });

  const onEmojiSelect = useLastCallback((emoji) => {
    setCustomStickerSelected(undefined);
    setCustomEmojiSelected(emoji.trim());
    dispatch({ type: 'setEmoticon', payload: emoji.trim() });
  });

  const handleCustomEmojiSelect = useLastCallback((sticker) => {
    setCustomStickerSelected(sticker);
    setCustomEmojiSelected(undefined);

    dispatch({
      type: 'setEmoticon',
      payload: (sticker.emoji ?? DEFAULT_FOLDER_ICON).trim(),
    });
  });

  const handleIconSelect = useLastCallback((emoticon) => {
    setCustomStickerSelected(undefined);
    setCustomEmojiSelected(emoticon.trim());
    dispatch({ type: 'setEmoticon', payload: emoticon.trim() });
  });

  return (
    <div className="settings-fab-wrapper" ref={ref}>
      <div className="settings-content no-border custom-scroll">
        <div className="settings-content-header">
          <AnimatedIconWithPreview
            size={STICKER_SIZE_FOLDER_SETTINGS}
            tgsUrl={LOCAL_TGS_URLS.FoldersNew}
            play={String(state.folderId)}
            className="settings-content-icon"
          />

          {isCreating && (
            <p
              className="settings-item-description mb-3"
              dir={lang.isRtl ? 'rtl' : undefined}
            >
              {lang('FilterIncludeInfo')}
            </p>
          )}

          <div className="input-with-button" ref={containerRef}>
            <InputText
              ref={inputRef}
              className="mb-0"
              label={lang('FilterNameHint')}
              value={state.folder.title.text}
              onChange={handleChange}
              // onKeyDown={}
              error={
                state.error && state.error === ERROR_NO_TITLE
                  ? ERROR_NO_TITLE
                  : undefined
              }
              maxLength={12}
            />

            {!isMobile ? (
              <ResponsiveHoverButton
                className={buildClassName(
                  'button-for-emoji',
                  isOpen && 'activated',
                )}
                color="translucent"
                onActivate={handleActivateSymbolMenu}
                ariaLabel="Choose emoji, sticker or GIF"
                isRectangular
              >
                <div ref={triggerRef} className="symbol-menu-trigger" />
                {customStickerSelected ? (
                  <div
                    style={customStickerSelected.shouldUseTextColor ? `filter:${isOpen
                      ? accentColorTheme : textColorTheme}` : ''}
                  >
                    <canvas ref={sharedCanvasRef} className="shared-canvas" />
                    <StickerView
                      containerRef={ref}
                      sticker={customStickerSelected}
                      size={EMOJI_SIZE_PICKER}
                      shouldLoop
                      shouldPreloadPreview
                      noLoad={!true}
                      noPlay={!canAnimate}
                      noVideoOnMobile
                      sharedCanvasRef={sharedCanvasRef}
                      withTranslucentThumb={false}
                    />
                  </div>
                ) : possibleIcon || !state.folder.emoticon ? (
                  <Icon name={possibleIcon ?? 'folder-badge'} />
                ) : (
                  state.folder.emoticon
                )}
              </ResponsiveHoverButton>
            ) : (
              <Button
                round
                size="smaller"
                color="translucent"
                className="button-for-emoji"
                onClick={handleClick}
              >
                {possibleIcon || !state.folder.emoticon ? (
                  <Icon name={possibleIcon ?? 'folder-badge'} />
                ) : (
                  state.folder.emoticon
                )}
              </Button>
            )}
            <Portal className="icon-folder-picker">
              <Menu
                style={
                  buildStyle(
                    'position: fixed;',
                    `top: ${contextMenuAnchor?.y}px;`,
                    isMobile ? 'right: 18rem;' : `left: ${contextMenuAnchor?.x}px;`,
                    isMobile && 'top: 15rem;',
                  )
                }
                isOpen={isOpen}
                onClose={handleMenuClose}
                withPortal={false}
                className={buildClassName('SymbolMenu')}
                onMouseEnter={!IS_TOUCH_ENV ? handleMouseEnter : undefined}
                onMouseLeave={!IS_TOUCH_ENV ? handleMouseLeave : undefined}
                noCloseOnBackdrop={!IS_TOUCH_ENV}
                noCompact
                // eslint-disable-next-line react/jsx-props-no-spreading
                {...{
                  positionX: 'left',
                  positionY: 'top',
                }}
              >
                <FolderIconPicker
                  onEmojiSelect={onEmojiSelect}
                  onCustomEmojiSelect={handleCustomEmojiSelect}
                  onIconSelect={handleIconSelect}
                  className="picker-tab"
                  isHidden={!isOpen || !isActive}
                  idPrefix="emoji-folder"
                  loadAndPlay={isOpen}
                  chatId=""
                  isTranslucent={!isMobile}
                />
              </Menu>
            </Portal>
          </div>
        </div>

        {!isOnlyInvites && (
          <div className="settings-item">
            {state.error && state.error === ERROR_NO_CHATS && (
              <p
                className="settings-item-description color-danger mb-2"
                dir={lang.isRtl ? 'rtl' : undefined}
              >
                {lang(state.error)}
              </p>
            )}

            <h4
              className="settings-item-header mb-3"
              dir={lang.isRtl ? 'rtl' : undefined}
            >
              {lang('FilterInclude')}
            </h4>

            <ListItem
              className="settings-folders-list-item color-primary"
              icon="add"
              narrow
              onClick={onAddIncludedChats}
            >
              {lang('FilterAddChats')}
            </ListItem>

            {renderChats('included')}
          </div>
        )}

        {!isOnlyInvites && !isEditingChatList && (
          <div className="settings-item pt-3">
            <h4
              className="settings-item-header mb-3"
              dir={lang.isRtl ? 'rtl' : undefined}
            >
              {lang('FilterExclude')}
            </h4>

            <ListItem
              className="settings-folders-list-item color-primary"
              icon="add"
              narrow
              onClick={onAddExcludedChats}
            >
              {lang('FilterAddChats')}
            </ListItem>

            {renderChats('excluded')}
          </div>
        )}

        <div className="settings-item pt-3">
          <h4
            className="settings-item-header mb-3"
            dir={lang.isRtl ? 'rtl' : undefined}
          >
            {lang('FolderLinkScreen.Title')}
          </h4>

          <ListItem
            className="settings-folders-list-item color-primary"
            icon="add"
            narrow
            onClick={handleCreateInviteClick}
          >
            {lang('ChatListFilter.CreateLinkNew')}
          </ListItem>

          {invites?.map((invite) => (
            <ListItem
              className="settings-folders-list-item"
              icon="link"
              narrow
              multiline
              onClick={handleEditInviteClick}
              clickArg={invite.url}
            >
              <span className="title" dir="auto">
                {invite.title || invite.url}
              </span>
              <span className="subtitle">
                {lang(
                  'ChatListFilter.LinkLabelChatCount',
                  invite.peerIds.length,
                  'i',
                )}
              </span>
            </ListItem>
          ))}
        </div>
      </div>

      <FloatingActionButton
        isShown={Boolean(state.isTouched)}
        disabled={state.isLoading}
        onClick={handleSubmit}
        ariaLabel={state.mode === 'edit' ? 'Save changes' : 'Create folder'}
      >
        {state.isLoading ? <Spinner color="white" /> : <Icon name="check" />}
      </FloatingActionButton>
    </div>
  );
};

export default memo(withGlobal<OwnProps>(
  (global, { state }): StateProps => {
    const { listIds } = global.chats;
    const { byId, invites } = global.chatFolders;
    const chatListCount = Object.values(byId).reduce((acc, el) => acc + (el.isChatList ? 1 : 0), 0);

    let customSticker: ApiSticker | undefined;
    let customEmoji: string | undefined;
    try {
      if (state.folderId) {
        const possibleEmoji = ((JSON.parse(localStorage.getItem(LOCSTOR_CUSTOM_EMOJI_KEY)
        ?? '')) as CustomEmojiIconsFolder)[state.folderId];
        if (typeof possibleEmoji === 'string') customEmoji = possibleEmoji;
        else customSticker = possibleEmoji;
      }
    } catch {
      localStorage.setItem(LOCSTOR_CUSTOM_EMOJI_KEY, JSON.stringify({}));
    }

    return {
      customSticker,
      customEmoji,
      loadedActiveChatIds: listIds.active,
      loadedArchivedChatIds: listIds.archived,
      invites: state.folderId ? (invites[state.folderId] || MEMO_EMPTY_ARRAY) : undefined,
      isRemoved: state.folderId !== undefined && !byId[state.folderId],
      maxInviteLinks: selectCurrentLimit(global, 'chatlistInvites'),
      maxChatLists: selectCurrentLimit(global, 'chatlistJoined'),
      chatListCount,
    };
  },
)(SettingsFoldersEdit));
